@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

*{
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
}

@mixin flex($direction,$justify) {
    display: flex;
    flex-direction: $direction;  
    align-items: center;
    justify-content: $justify;  
}



body{
    overflow-y: hidden;
}

.App{
    height: 100%;
    overflow: hidden;
    //Navbar 
    .Nav{
        display: grid;
        grid-template-columns: 25% 50% 25%;
        padding: 5px;
        .Nav-left{
            margin:10px 12px;
            @include flex(row, left);
            img{
                height:18px;
                padding-left:15px;
                cursor: pointer;
            }
            padding: 0px 10px;
        }
        .Nav-center{
            @include flex(row, center);
            .Search{
                input{
                    height: 38px;
                    border-radius: 2px;
                    border: 1px solid rgba(225, 225, 225, 1);
                    width: 500px;
                    padding: 12px;
                    &:focus{
                        outline: none;
                    }
                }

                
                @include flex(row, center);
                .icon{
                    border:1px solid rgba(225, 225, 225, 1);
                    border-radius: 2px;
                    height: 38px;
                    width: 55px;
                    @include flex(row, center);
                    cursor: pointer;
                }
            }
        }
        .Nav-right{
            @include flex(row, right);
            .circle{
                height: 30px;
                cursor: pointer;
                width: 30px;
                border-radius: 100%;
                background-color: white;
                border: 1px solid grey;
            }
        }
        .Nav-right > *{
            margin:0px 18px;
            cursor: pointer;
        }
    }

    //Content
    .content{
        overflow: hidden;
        @include flex(row,left);
        .Selection{
            width: 66px;
            height: 95vh;
            @include flex(column, top);
            .box{
                height: 70px;
                width: 75px;
                cursor: pointer;
                .icon-design{
                    font-size: 120px
                }
                @include flex(column, center);
                p{
                    font-size: 10px;
                }
                &:hover{
                    background-color: rgba(225, 225, 225, 1);
                }
            }
        }
        .Fullselection{
            width: auto;
            height: 95vh;
            @include flex(column,top);
            .box{
                @include flex(row,space-between);
                cursor: pointer;
                font-size:7px;
                padding:8px 30px 8px 20px;
                &:hover{
                    background-color: rgba(225, 225, 225, 0.5);
                }
                .icon-design{
                    width: 38px;
                    height:25px;
                }
                h1{
                    width: 120px;
                    height: 18px;
                    font-family: 'Roboto Condensed', sans-serif;

                }
            }
            .line{
                width: 200px;
                background-color:rgba(225, 225, 225, 0.8);
                height: 1px;
                margin: 15px 0px; 
            }
        }
        .Main{
            width: 100%;
            height: 95vh;

            @include flex(column, top);
            .suggestions{
                @include flex(row, left);
                width:100%;
                height: 75px;
                border-top: 1px solid rgba(225, 225, 225, 1);
                border-bottom: 1px solid rgba(225, 225, 225, 1);
                .Suggestion{
                    font-size: 13px;
                    cursor: pointer;
                    border-radius: 25px;
                    padding: 5px 15px;
                    margin-left: 12px;
                    background-color: rgba(225, 225, 225, 0.5);
                    border: 1px solid rgba(225, 225, 225, 1);
                    &:hover{
                        background-color: rgba(225, 225, 225, 1);
                    }
                }
            }
            .items{
                @include flex(row, left);
                flex-wrap: wrap;
                padding: 12px 25px;
                overflow-y: scroll;
                width: 100%;
                .Card{
                    cursor: pointer;
                    margin: 25px;
                    img{
                        height: 160px;
                        max-width: 400px;
                    }
                    .informations{
                        display: flex;
                        justify-content: left;
                        img{
                            border-radius: 100%;
                            height:37px;
                            width: 37px;
                        }
                        .infos{
                            padding-left: 10px;
                            h5{
                                font-size:18px;
                            }
                            p{
                                color: grey;
                                font-size: 14px;
                            }
                        }
                    }
                    
                    &:hover{
                        transition: all 0.2s ease-in-out;
                        transition-delay: 1s;

                        scale: 1.1;
                    }
                }
            }
        }
    }

}

@media screen and (max-width:1040px) {
    .App{
        .Nav{
            .Nav-center{
                .Search{
                    input{
                        width: 350px;
                    }
                }
                
            }
        }
    }
}
@media screen and (max-width:800px) {
    body{
        overflow: hidden;
    }
    .App{
        .Nav{
            .Nav-left{
                margin: 0px 0px;
                img{
                    height: 10px;
                }
                padding: 0px 5px;
            }
            .Nav-center{
                .Search{
                    input{
                        width: 100%;
                    }
                }
                
            }
        }
        .content{

            .Main{
                .items{
                    padding: 0px;
                    .Card{
                        .img{
                            width: 600px;
                            height: 200px;
                        }
                    }
                }
            }
        }

    }
}

@media screen and (max-width:420px) {
    body{
        overflow: hidden;
    }
    .App{
        .Nav{
            .Nav-left{
                margin: 0px 0px;
                img{
                    height: 10px;
                }
                padding: 0px 5px;
            }
            .Nav-center{
                .Search{
                    input{
                        width: 100%;
                    }
                }
                
            }
        }
        .content{
            .Selection{
                flex-direction: row;
                position:fixed;
                width: 100%;
                height: fit-content;
                top: 98%;
                left: 50%;
                transform: translate(-50%,-50%);
                .box{
                    padding: 15px;
                }
            }
            .Selection>*{
                background-color: white;
            }
            .Main{
                .items{
                    .Card{
                        width: 100%;
                        img{
                            height: 150px;
                        }
                    }
                }
            }
        }

    }
}
